@tailwind base;
@tailwind components;
@tailwind utilities;
body {
  font-family: "Urbanist", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  font-family: "Reem Kufi", sans-serif;
  font-optical-sizing: auto;
  font-weight: 700;
  font-style: normal;
}

.menu {
  display: flex;
}
.menu > li > a {
  color: white;
}
.menu > li + li {
  margin-left: 2rem;
}
.menu .menu__dropdown {
  display: none;
}